<template>
  <div id="app">
    <h1>{{title}}</h1>
    <el-form v-if="!linkTimeout" :model="form" :rules="rules" status-icon ref="elForm">
      <el-form-item :label="$t('App.pwdPlaceholder')" prop="upwd">
        <el-input v-model="form.upwd" type="password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item :label="$t('App.pwdPlaceholder')" prop="conflictingPwd">
        <el-input v-model="form.conflictingPwd" type="password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="commit">{{$t('App.commit')}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {Form, FormItem, Input, Button, Message} from 'element-ui';
import axios from 'axios'
import crypto from 'crypto'
import Qs from 'qs'

export default {
  name: 'App',
  data () {
    const that = this
    var validatePass = (rule, value, callback) => {
      if (value == '') {
        callback(new Error(that.$t('App.invalidPwd')));
      } else {
        if (that.form.conflictingPwd !== '') {
          that.$refs.elForm.validateField('conflictingPwd');
        }
        callback();
      }
        console.log("Pwd: ", value);
        console.log("conflict: ", that.form.conflictingPwd);
        console.log("Equal: ", value === that.form.conflictingPwd)
    };
    var validatePass2 = (rule, value, callback) => {
      if (value == '') {
        callback(new Error(that.$t('App.pwdRepeatPlaceholder')));
      } else if (value !== that.form.upwd) {
        callback(new Error(that.$t('App.conflictingPwdError')));
      } else {
        callback();
      }
        console.log("Pwd2: ", that.form.upwd);
        console.log("conflict2: ", that.form.conflictingPwd);
        console.log("Equal2: ", value !== that.form.upwd)
    };
    return {
      form: {
        upwd: "",
        code: "",
        conflictingPwd: ""
      },
      rules: {
        upwd: [
          // { required: true, message: that.$t('App.invalidPwd'), trigger: 'blur' }
          { validator: validatePass, trigger: 'blur' }
        ],
        conflictingPwd: [
          // { required: true, message: that.$t('App.pwdRepeatPlaceholder'), trigger: 'blur' }
          { validator: validatePass2, trigger: 'blur' }
        ],
      },
      linkTimeout: true,
      title: ""
    }
  },
  watch: {
    linkTimeout () {
      if (this.linkTimeout)
        this.title = this.$t('App.timeout')
      else
        this.title = this.$t('App.title')
    }
  },
  methods: {
    getQueryString(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null)
          return unescape(r[2]);
      return "";
    },
    commit () {
      var that = this
      this.$refs['elForm'].validate(valid => {
        if (valid) {
          var upwd = crypto.createHash("md5").update(that.form.upwd).digest("hex")
          that.netIf({
            url: "/worker/reg",
            data: {
              upwd,
              code: that.form.code
            },
            succeed: res => {
              if (res.data.statecode == 1){
                Message({message: that.$t('App.activeSuccess')})
              } else {
                Message({message: that.$t('App.activeFailed')})
                console.log(res.data)
              }
            },
            failed: error => {
              Message({message: that.$t('App.activeFailed')})
              console.log(error)
            }
          })
        } else {
          Message({message: that.$t('App.formError')})
          return false
        }
      })
    },
    netIf: (options) => {
      var succeed = options.succeed
      delete options.succeed
      var failed = options.failed
      delete options.failed
      axios({
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        transformRequest: function (data, headers) {
          return Qs.stringify(data, {arrayFormat: 'brackets'})
        },
        url: options.url,
        data: options.data,
        method: "post"
      })
      .then(succeed)
      .catch(failed)
    }
  },
  mounted () {
    // if (process.env.NODE_ENV === "development") {
    //   this.linkTimeout = false
    //   return
    // }
    if (this.linkTimeout)
      this.title = this.$t('App.timeout')
    var code = this.getQueryString('activecode')
    var that = this
    if (code !== "")
      this.form.code = code
    else{
      that.title = that.$t('App.urlCodeError')
      return
    }
    this.netIf({url: "/worker/regbefore", data: {code},
      succeed: res => {
        if (res.data.statecode == 16)
          that.linkTimeout = false
        else that.title = that.$t('App.timeout')
      },
      failed: error => {
        Message({message: that.$t('App.serverError')})
        console.log(error)
      }
    })
  },
  components: {
    ElForm: Form,
    ElFormItem: FormItem,
    ElInput: Input,
    ElButton: Button,
    Message
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 60px auto;
  width: 60%;
}
</style>



